// title
@mixin title-h1 {
  font-family: $ff-Alumni;
  font-style: normal;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  color: $color-default-white;
  margin: 0;
}

@mixin title-h2 {
  font-family: $ff-Alumni;
  font-style: normal;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  color: $color-sapphire;
  margin: 0;
  letter-spacing: 0.03em;
}

@mixin title-h3 {
  font-family: $ff-roboto;
  font-style: normal;
  font-weight: 700;
  font-size: 26px;
  line-height: 29px;
  color: $color-sapphire;
  margin: 0;
}

@mixin title-h4 {
  font-family: $ff-roboto;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: $color-sapphire;
  margin: 0;
}

// button
@mixin button {
  display: inline-block;
  font-family: $ff-roboto;
  background-color: transparent;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  color: $color-sapphire;
  cursor: pointer;
}

