@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-light.woff2") format("woff2"), url("../fonts/RobotoCondensed-light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-ExtraBold.woff2") format("woff2"), url("../fonts/AlumniSans-ExtraBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  max-width: 1160px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
  .container--mobile {
    padding: 0;
  }
}

.header {
  margin-top: 30px;
}
@media (max-width: 1023px) {
  .header {
    margin-top: 34px;
  }
}
@media (max-width: 767px) {
  .header {
    margin-top: 15px;
  }
}
.header__logo {
  fill: #ffffff;
}
@media (max-width: 1023px) {
  .header__logo {
    width: 82px;
    height: 17px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 69px;
    height: 24px;
  }
}
.header__nav {
  display: flex;
  justify-content: space-between;
  margin-bottom: -34px;
}
.header__nav a {
  display: block;
  color: #ffffff;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  max-width: 154px;
  z-index: 2;
}
@media (max-width: 1023px) {
  .header__nav a {
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .header__nav a {
    font-size: 20px;
  }
}
.header__nav a:hover {
  opacity: 0.7;
}
.header__nav a.focus-visible:focus {
  opacity: 0.7;
}
.header__nav a:active {
  opacity: 0.5;
}

.main-box {
  background: conic-gradient(from 150.87deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  padding-bottom: 54px;
}
@media (max-width: 1023px) {
  .main-box {
    padding-bottom: 75px;
  }
}
@media (max-width: 767px) {
  .main-box {
    padding-bottom: 79px;
  }
}

.general {
  display: flex;
  align-items: center;
  column-gap: 10px;
}
@media (max-width: 1023px) {
  .general {
    justify-content: center;
  }
}
.general__box {
  text-align: right;
  margin-top: 120px;
  margin-left: 19px;
}
@media (max-width: 1023px) {
  .general__box {
    margin-top: 90px;
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .general__box {
    margin-top: 102px;
  }
}
@media (max-width: 767px) {
  .general__inner {
    margin-top: 7px;
  }
}
@media (max-width: 1023px) {
  .general__picture {
    margin-bottom: 7px;
    margin-top: 53px;
  }
}
@media (max-width: 767px) {
  .general__picture {
    display: none;
  }
}
.general ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .general ul {
    min-width: 113px;
  }
}
.general__item {
  font-size: 14px;
  line-height: 21px;
}
@media (max-width: 1023px) {
  .general__img {
    width: 324px;
    height: 390px;
  }
}
.general__container {
  max-width: 310px;
  margin-top: 76px;
}
@media (max-width: 1023px) {
  .general__container {
    margin-top: 74px;
    max-width: 221px;
  }
}
@media (max-width: 767px) {
  .general__container {
    margin-left: -22px;
    margin-top: 99px;
    max-width: 189px;
  }
}
.general__container span {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 44px;
  line-height: 58px;
  letter-spacing: 0.03em;
  color: #ffffff;
  align-self: center;
  text-transform: uppercase;
  margin-left: 121px;
  margin-bottom: 10px;
  display: inline-block;
}
@media (max-width: 1023px) {
  .general__container span {
    font-size: 31px;
    line-height: 41px;
    margin-left: 139px;
    margin-bottom: 4px;
  }
}
@media (max-width: 767px) {
  .general__container span {
    line-height: 34px;
    margin-left: 138px;
    margin-top: -3px;
    font-size: 25px;
  }
}
.general__container h1 {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  color: #ffffff;
  margin: 0;
  letter-spacing: 0.05em;
  font-weight: 800;
  text-align: right;
  text-transform: uppercase;
  margin: 0px;
  margin-right: 55px;
  margin-bottom: 20px;
}
@media (max-width: 1023px) {
  .general__container h1 {
    font-size: 72px;
    line-height: 86px;
    margin: 0 19px 17px 0;
    text-transform: capitalize;
    letter-spacing: 0.04em;
  }
}
@media (max-width: 767px) {
  .general__container h1 {
    margin: 0;
  }
}
.general__button {
  display: inline-block;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  background-color: transparent;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  color: #102654;
  cursor: pointer;
  border: 4px solid #ffffff;
  color: #ffffff;
  padding: 24px 56px;
  text-transform: uppercase;
  padding: 20px 54px;
  margin-left: 22px;
  margin-top: 27px;
  letter-spacing: 0.05em;
}
@media (max-width: 1023px) {
  .general__button {
    padding: 20px 31px;
    margin-left: 17px;
    margin-top: 19px;
  }
}
@media (max-width: 767px) {
  .general__button {
    padding: 20px 83px;
    margin-left: -102px;
    margin-top: 23px;
  }
}
.general__button:hover {
  opacity: 0.7;
}
.general__button.focus-visible:focus {
  opacity: 0.7;
}
.general__button:active {
  opacity: 0.5;
}

.gym {
  padding-top: 120px;
  padding-bottom: 10px;
}
@media (max-width: 1023px) {
  .gym {
    padding-top: 100px;
    padding-bottom: 16px;
  }
}
@media (max-width: 767px) {
  .gym {
    padding-top: 65px;
    padding-bottom: 70px;
  }
}

.gym__container {
  display: grid;
  grid-template-columns: 1fr 1.59fr;
  column-gap: 85px;
  row-gap: 0;
}
@media (max-width: 1023px) {
  .gym__container {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 767px) {
  .gym__container {
    padding: 0;
    row-gap: 33px;
  }
}

.gym__description {
  padding-left: 60px;
  padding-top: 40px;
  grid-column: 1/2;
  grid-row: 1/4;
}
@media (max-width: 1023px) {
  .gym__description {
    grid-column: 1/-1;
    grid-row: 1/2;
    padding: 0;
    padding-left: 0;
    padding-bottom: 42px;
  }
  .gym__description p + p {
    display: none;
  }
}
@media (max-width: 767px) {
  .gym__description {
    padding: 0 15px;
  }
}
.gym__description h2 {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  color: #102654;
  margin: 0;
  letter-spacing: 0.03em;
  margin-bottom: 6px;
}
@media (max-width: 1023px) {
  .gym__description h2 {
    font-size: 42px;
    line-height: 50px;
    text-align: center;
    margin-bottom: 0;
    transform: translateX(-9px);
  }
}
@media (max-width: 767px) {
  .gym__description h2 {
    font-size: 32px;
    line-height: 38px;
    text-align: left;
    margin-bottom: 3px;
    transform: translateX(0);
  }
}
.gym__description strong {
  display: block;
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
}
@media (max-width: 1023px) {
  .gym__description strong {
    text-align: center;
    margin-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .gym__description strong {
    text-align: left;
    margin-bottom: 25px;
  }
}
.gym__description p {
  margin: 0;
  margin-bottom: 8px;
  padding: 0;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .gym__description p {
    max-width: 510px;
    margin: 0 auto;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .gym__description p {
    padding-right: 30px;
    text-align: left;
    margin: 0;
  }
}

.gym__image {
  box-sizing: border-box;
  max-width: 660px;
  width: 100%;
  aspect-ratio: 660/401;
  grid-column: 2/3;
  place-self: start start;
  background-color: #afafaf;
}
@media (max-width: 1023px) {
  .gym__image {
    grid-column: 1/-1;
    grid-row: 2/3;
    max-width: none;
    aspect-ratio: 678/345;
  }
}
@media (max-width: 767px) {
  .gym__image {
    aspect-ratio: 320/313;
  }
}
.gym__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-color: #afafaf;
}

.gym__video {
  display: block;
  width: 33%;
  max-width: 364px;
  aspect-ratio: 364/228;
  grid-column: 1/3;
  grid-row: 2/3;
  place-self: start center;
  transform: translateY(-50%) translateX(11%);
  background-color: #b6bdcc;
}
@media (max-width: 1023px) {
  .gym__video {
    width: 39.2%;
    grid-column: 1/-1;
    grid-row: 3/4;
    aspect-ratio: 266/167;
    transform: translateY(-50%) translateX(0);
  }
}
@media (max-width: 767px) {
  .gym__video {
    width: 100%;
    max-width: none;
    margin-top: 7px;
    aspect-ratio: 320/167;
    transform: translate(0, 0);
  }
}

.gym__video-cover {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 100%;
}
.gym__video-cover.hidden {
  display: none;
}
.gym__video-cover::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #14367a;
  opacity: 0.5;
}
.gym__video-cover img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-color: #afafaf;
}

.gym__video-button {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 72px;
  height: 72px;
  border: none;
  border-radius: 50%;
  background: linear-gradient(44.89deg, #14367a 25.38%, #b80027 74.89%);
  cursor: pointer;
  transform: translate(-50%, -50%);
}
.gym__video-button svg {
  transform: translateX(2px);
}
@media (max-width: 1023px) {
  .gym__video-button {
    width: 52px;
    height: 52px;
  }
  .gym__video-button svg {
    width: 12px;
    height: 12px;
    transform: translateX(1px);
  }
}
@media (max-width: 767px) {
  .gym__video-button svg {
    transform: translateX(3px);
  }
}
.gym__video-button::after {
  content: "";
  position: absolute;
  top: 50%;
